/* Container lớn hơn */
.chatbot-container {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 420px;
  height: 500px;
  background: var(--card-bg);
  border: 1px solid var(--text-medium);
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 4px 20px var(--shadow-light);
  font-family: Arial, sans-serif;
  z-index: 9999;
  display: flex;
  flex-direction: column;
  transition: background-color 0.3s ease, border-color 0.3s ease;
}

/* Thanh header */
.chatbot-header {
  background: rgba(249, 119, 53, 1);
  color: white;
  padding: 12px 16px;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Nội dung */
.chatbot-body {
  flex: 1;
  overflow-y: auto;
  padding: 12px;
  background: var(--primary-bg);
  transition: background-color 0.3s ease;
}

/* Tin nhắn */
.chat-message {
  padding: 8px 12px;
  margin-bottom: 10px;
  border-radius: 15px;
  max-width: 80%;
  clear: both;
  white-space: pre-line;
}

.chat-message.bot {
  background: var(--card-bg);
  color: var(--text-dark);
  float: left;
  border: 1px solid var(--text-medium);
  transition: background-color 0.3s ease, color 0.3s ease,
    border-color 0.3s ease;
}

.chat-message.user {
  background: rgba(249, 119, 53, 1);
  color: white;
  float: right;
}

/* Input */
.chatbot-input {
  display: flex;
  border-top: 1px solid var(--text-medium);
  transition: border-color 0.3s ease;
}

.chatbot-input input {
  flex: 1;
  padding: 12px;
  border: none;
  outline: none;
  background-color: var(--primary-bg);
  color: var(--text-dark);
  transition: background-color 0.3s ease, color 0.3s ease;
}

.chatbot-input button {
  background: rgba(249, 119, 53, 1);
  color: white;
  border: none;
  padding: 12px 18px;
  cursor: pointer;
}

/* Nút X và Thu nhỏ */
.close-btn,
.minimize-btn {
  background: none;
  border: none;
  color: white;
  font-size: 1.2rem;
  cursor: pointer;
  margin-left: 10px;
}

/* Nút tròn nhỏ sau khi thu gọn */
.chatbot-icon-button {
  position: fixed;
  bottom: 80px;
  right: 30px;
  width: 60px;
  height: 60px;
  background: rgb(255, 85, 0);
  border: 1px solid rgba(0, 0, 0, 0.2); /* Viền đen mờ */
  border-radius: 50%;
  box-shadow: 0 4px 10px var(--shadow-light); /* Mờ hơn để tạo chiều sâu */
  cursor: pointer;
  z-index: 9999;
  padding: 0;
  overflow: visible;
  display: flex;
  justify-content: center;
  align-items: center;
}

.chatbot-icon-button img {
  width: 32px;
  height: 32px;
}

.chatbot-title {
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: bold;
  font-size: 14px;
}

/* Button bao quanh logo */
.chatbot-logo-button {
  background-color: var(--primary-bg);
  border: none;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
  cursor: default;
  box-shadow: 0 0 3px var(--shadow-light);
  transition: background-color 0.3s ease;
}

/* Logo bên trong */
.chatbot-logo {
  width: 22px;
  height: 22px;
  object-fit: contain;
}

